.frc-page {
    display: grid;
    grid-template: -webkit-min-content 1fr / auto;
    grid-template: min-content 1fr / auto;
    grid-column: 2;
}

.frc-page_header {
    padding: 1em;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    -webkit-transition: background-color 0.1s ease-in;
    -o-transition: background-color 0.1s ease-in;
    transition: background-color 0.1s ease-in;

    display: grid;
    grid-template: auto / -webkit-min-content 1fr;
    grid-template: auto / min-content 1fr;
    grid-gap: 1em;
}

.frc-page_header:hover {
    background-color: rgba(255, 255, 255, 0.1)
}

.frc-page_main {
    display: grid;
    grid-template: -webkit-min-content 1fr -webkit-min-content / auto;
    grid-template: min-content 1fr min-content / auto;
}

.frc-page_main > header {
    border-bottom: 1px solid rgba(255,255,255,0.2);
    cursor: pointer;
}

.frc-nav-list {
    position: relative;
    overflow: auto;
}


.frc-nav-page {
    display: grid;
    grid-template: -webkit-min-content min-content 1fr / auto;
    grid-template: min-content min-content 1fr / auto;
    grid-column: 2;
}


.frc-nav-page_header {
    padding: 1em;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    -webkit-transition: background-color 0.1s ease-in;
    -o-transition: background-color 0.1s ease-in;
    transition: background-color 0.1s ease-in;
    display: grid;
    grid-template: auto / -webkit-min-content 1fr;
    grid-template: auto / min-content 1fr;
    grid-gap: 1em;
}

.frc-nav-page_header:hover {
    background-color: rgba(255, 255, 255, 0.1)
}

.frc-nav-page_title {
    padding: 1em;
    border-bottom: 1px solid rgba(255,255,255,0.2);
    font-size: 1.5rem;
}



