.frc-movement_header {
    display: grid;
    grid-template: auto / 1fr 1fr;
}

.frc-movement_header > * {
    padding: 1em;
}

.frc-movement_prev {
    display: grid;
    grid-template: auto / -webkit-min-content 1fr;
    grid-template: auto / min-content 1fr;
    grid-gap: 1em;
}

.frc-movement_next {
    display: grid;
    grid-template: auto / 1fr -webkit-min-content;
    grid-template: auto / 1fr min-content;
    grid-gap: 1em;
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    text-align: right;
}

.frc-movement_prev
.frc-movement_next {
    -webkit-transition: background-color 0.1s ease-in;
    -o-transition: background-color 0.1s ease-in;
    transition: background-color 0.1s ease-in;
}

.frc-movement_prev:hover,
.frc-movement_next:hover {
    background-color: rgba(255, 255, 255, 0.1)
}


