* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Titillium Web', sans-serif;
  background: #111;
  color: white;
  font-size: 22px;
  height: 100%;
  display: grid;
  grid-template: auto / 1fr;
}

#root {
  display: grid;
  grid-template: auto / 1fr minmax(300px, 700px) 1fr;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 1.7em;
}
h2 {
  font-size: 1.5em;
}
h3 {
  font-size: 1.3em;
}



